import React from 'react';
import logo from './react.svg';
import axios from 'axios';
import JSONPretty from 'react-json-pretty';
import { Input, Icon, Layout, Row, Col, Button, Typography } from 'antd';
import autobind from 'autobind-decorator'
import 'react-json-pretty/themes/monikai.css';

const { TextArea  } = Input;
const { Text } = Typography;
const { Content, Header } = Layout;

import './Home.css';

class Home extends React.Component {
	state = {
		source: null,
		index: '',
		query: '',
		control: '',
		change: ''
	}
	@autobind
	changeItem(key, val) {
		let obj = {};
		obj[key] = val;
		this.setState(obj)
	}
	@autobind
	createSample() {
		const { source, change, control } = this.state;
		axios.post('/run', {
			source: JSON.stringify(source),
			control,
			change
		}).then(res => console.log(res))
	}
	@autobind
	queryData() {
		axios.post('/query', {
			index: this.state.index,
			query: this.state.query
		}).then(res => {
			if (res.data.data) {
				this.setState({
					source: res.data.data._source
				})
			}
		})
	}
  render() {
	  const { index, query, source, change, control } = this.state;
    return (
      <div className="Home">
			<Layout>
				<Header className="header">
					<Text type="warning">造数据</Text>
				</Header>
			  <Content  style={{ padding: '15px'  }}>
					<Row>
						  <Col span={6}>
							  <Input placehoder="输入索引名称" value={index} onChange={(e) => this.changeItem("index", e.target.value)}/>
							  <TextArea placeholder="输入查询语句" autosize={{minRows: 4, maxRows: 18}} onChange={(e) => this.changeItem("query", e.target.value)} onBlur={this.queryData}/>
						</Col>
					  <Col span={18}>
						  <div className="code">
							  <JSONPretty data={source}></JSONPretty>
						  </div>
						  <TextArea autosize={{minRows: 2, maxRows: 6}} onChange={(e) => this.changeItem("change", e.target.value)} value={change} placeholder="修改内容"/>
						  <TextArea autosize={{minRows: 5, maxRows: 5}} onChange={(e) => this.changeItem("control", e.target.value)} value={control} placeholder="控制信息&#10;repeat:3 &#10; total:30&#10; index:tdp-2019-0808"/>
						<Button type="primary" onClick={this.createSample}>生成样例</Button>
						<Button type="danger">开始创建</Button>
					</Col>
					</Row>

			  </Content>
			</Layout>
      </div>
    );
  }
}

export default Home;
